import React from 'react'
import randomColor from 'randomcolor';
import invert from 'invert-color';
import $ from 'jquery';

function set_mobile_theme() {
    var color = randomColor();
    var theme = document.querySelector('meta[name="theme-color"]').setAttribute('content', color)
}


function set_background_color_for_navbar() {
    const theme_color = document.querySelector('meta[name="theme-color"]').getAttribute('content');
    const color = invert(theme_color)
    const style = `<style id="navbar_style">.MuiPaper-root.MuiDrawer-paper.MuiDrawer-paperAnchorLeft.MuiPaper-elevation16 {background-color: ${color}}</style>`
    $('#navbar_style').remove();
    $('head').append(style);
}

export {
    set_mobile_theme,
    set_background_color_for_navbar
}